<script>
import { mapGetters, mapMutations } from "vuex";
export default {
  data() {
    return {
      user: [],
      orderStatusNum: [],
      mymenu: [],
    };
  },
  computed: {
    ...mapGetters(["userInfo", "token"]),
  },
  created() {
    //订单数据
    this.$axios
      .get("/api/order/list?page=1&limit=20&type=0", {
        params: {
          token: this.token,
        },
      })
      .then((d) => {
        console.log(d);
      });

    //用户信息
    this.$axios
      .get("/api/user", {
        params: {
          token: this.token,
        },
      })
      .then((d) => {
        if (d.data.status == 200) {
          console.log("d==>", d);
          this.user = d.data.data;
          this.orderStatusNum = d.data.data.orderStatusNum;
        } else {
          this.$router.push("/login");
        }
      });
    this.$axios
      .get("http://47.115.51.185/api/menu/user", {
        params: {
          token: this.token,
        },
      })
      .then((data) => {
        if (data.data.status == 200) {
          this.mymenu = data.data.data.routine_my_menus;
          console.log(this.mymenu);
        } else {
          this.$router.push("/login");
        }
      });
  },
  methods: {
    ...mapMutations({
      logintoken: "loginOut",
    }),
    loginOut() {
      this.logintoken();
      this.$router.push("/login");
    },
  },
};
</script>
